html {
  --font-color: #3d4e61;
}

@font-face {
  font-family: Avenir;
  font-weight: 300;
  src: url("Avenir-Book.657c3568.ttf");
}

@font-face {
  font-family: Avenir;
  font-weight: 900;
  src: url("Avenir-Heavy.01d9ab74.ttf");
}

body {
  font-family: Avenir, sans-serif;
}

.search-bar {
  margin-top: 20px;
}

.search-bar__input {
  box-sizing: border-box;
  color: var(--font-color);
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  outline: none;
  width: 100%;
  font-family: inherit;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search-bar--big {
  background-position: left 26px bottom 25px;
  background-size: 19px;
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
}

.search-bar--small {
  background-position: left 12px bottom 15px;
  background-size: 11px;
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
}

.search-bar--big::placeholder, .search-bar--small::placeholder {
  color: var(--font-color);
  font-weight: 300;
}

.search-bar__input:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.search-bar__input:focus {
  border-radius: 3px;
  font-weight: 900;
  box-shadow: 0 4px 4px #3d4e6133;
